import React, { useContext, useEffect, useMemo, useState, useRef } from "react";
import { Context } from "../../context/DatesContext";
import { Context as FormContext } from "../../context/FormContext";
import { FormContainer } from "../Form/FormContainer";
import { Context as IdContext } from "../../context/IdContext";
import { GridCell } from "./GridCell";
// import { Context as FormContext } from "../../context/FormContext";
import { Button } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import "./Grid.css";
import { MonthRef2 } from "./MonthRef2";
import { Rented } from "./Rented";

import { RentedDescription } from "./RentedDescription";
import { subDays, isWeekend } from "date-fns";
import { ScrollBar } from "../Scrollbar/ScrollBar";
import { boxSizing, margin } from "@mui/system";
import { NavDays } from "../NavDays/NavDays";
import { ka } from "date-fns/locale";
import { RoomNumbers } from "./RoomNumbers";
import zIndex from "@mui/material/styles/zIndex";
export const Grid = ({ ws }) => {
  const rentedRef = useRef(null);
  const containerRef = useRef(null);
  const containerRef2 = useRef(null);
  const cellRef = useRef(null);
  const [w, setW] = useState(0);
  const [h, setH] = useState(0);
  const [cellH, setCellH] = useState(0);
  const [containerWidth, setContainerWidth] = useState(0);
  const [containerLeft, setContainerLeft] = useState(0);

  const [interval, setInterval] = useState(8);

  const {
    state: {
      calendar: { intervalRented },
    },
  } = useContext(Context);
  const handleAddReservation = () => {
    const today = new Date();
    showFormMsg({
      showForm: true,
      start: today,
      room: 1, // Puedes ajustar esto segn tus necesidades
      price: 0, // Puedes ajustar esto segn tus necesidades
      formType: "create",
    });
  };

  useEffect(() => {
    if (containerRef.current !== null) {
      // console.log(containerRef.current.getBoundingClientRect());
      setContainerWidth(containerRef.current.getBoundingClientRect().width);
      setContainerLeft(containerRef.current.getBoundingClientRect().left);
      // setContainerWidth2(containerRef2.current.getBoundingClientRect().width);
      // setContainerLeft2(containerRef2.current.getBoundingClientRect().left);
    }
  }, [[interval]]);
  // console.log('containerWidth', containerWidth);
  useEffect(() => {
    if (rentedRef.current !== null) {
      // setH(rentedRef.current.getBoundingClientRect().height);
      setW(rentedRef.current.getBoundingClientRect().width);
      setCellH(cellRef.current.getBoundingClientRect().height);
    }
  }, [interval]);
  useEffect(() => {
    const onResize = (e) => {
      setW(rentedRef.current.getBoundingClientRect().width);
      // setH(rentedRef.current.getBoundingClientRect().height);
      setCellH(cellRef.current.getBoundingClientRect().height);
      setContainerWidth(containerRef.current.getBoundingClientRect().width);
      setContainerLeft(containerRef.current.getBoundingClientRect().left);
    };
    window.addEventListener("resize", onResize);
    return () => {
      window.removeEventListener("resize", onResize);
    };
  });

  const { state, pos_Msg } = useContext(Context);

  const { calendar, hotelRooms, clicked } = state;
  // console.log(clicked);
  const { intervalData, intervalMonth, intervalRentedArray } = calendar;

  const { showFormMsg } = useContext(FormContext);

  const showFormHandler = (event, start, room, price) => {
    event.preventDefault();
    event.stopPropagation();
    showFormMsg({ showForm: true, start, room, price, formType: "create" });
  };

  const {
    state: { id },
    idMsg,
  } = useContext(IdContext);

  const [pos, setPos] = useState({ x: 0, y: 0 });

  const handlePos = (e) => {
    e.preventDefault();
    if (clicked) {
      e.stopPropagation();
      setPos(pos);
    } else {
      setPos({ x: e.clientX - 10, y: e.clientY + 10 });
    }
  };
  const handleFixPos = (e) => {
    if (clicked) {
      e.stopPropagation();
    }
    setPos(pos);
  };

  const grid = useMemo(() =>
    hotelRooms.map((room, r) => {
      const roomNumber = r + 1;
      return intervalData.slice(0, interval).map((day, i) => {
        const rented = intervalRentedArray.find((rented) => {
          return (
            day.date === rented.start && Number(rented.room) === roomNumber
          );
        });

        return (
          <GridCell
            ws={ws}
            containerRef={containerRef.current}
            gridDataDrag={{ room: roomNumber, day }}
            containerLeft={containerLeft}
            containerWidth={containerWidth}
            rented={rented}
            index={i}
            reference={rentedRef}
            className={`${
              isWeekend(day.formDate)
                ? "grid-item-rooms weekend"
                : "grid1 grid-item-rooms"
            }`}
            w={w}
            onDClick={(e) => {
              showFormHandler(
                e,
                subDays(day.formDate, 1),
                roomNumber,
                e.target.value
              );
            }}
            interval={interval}
            room={roomNumber}
            day={subDays(day.formDate, 1)}
            formdate={day.formDate}
            cellRef={cellRef}
            cellH={cellH}
            // pos={pos}
          />
        );
      });
    })
  );

  const intervalGrid2 = intervalData.slice(interval - 1, 120).length;

  const grid2 = useMemo(() =>
    hotelRooms.map((room, r) => {
      const roomNumber = r + 1;
      return intervalData.slice(interval - 1, 120).map((day) => {
        // console.log(day);
        const rented = intervalRentedArray.find((rented) => {
          return (
            day.date === rented.start && Number(rented.room) === roomNumber
          );
        });

        return (
          <div
            className="grid-item-rooms grid2"
            onDoubleClick={(e) => {
              showFormHandler((day.formDate, roomNumber), roomNumber);
            }}
          >
            {/* {roomNumber} */}
            {rented ? (
              <Rented
                ws={ws}
                containerRef={containerRef2.current}
                gridDataDrag={{ room: roomNumber, day }}
                containerLeft={containerLeft}
                containerWidth={containerWidth}
                rented={rented}
                // index={i}
                reference={rentedRef}
                className={`${
                  isWeekend(day.formDate)
                    ? "grid-item-rooms weekend"
                    : "grid1 grid-item-rooms"
                }`}
                w={w}
                onDClick={(e) => {
                  showFormHandler(
                    e,
                    subDays(day.formDate, 1),
                    roomNumber,
                    e.target.value
                  );
                }}
                interval={interval}
                room={roomNumber}
                day={subDays(day.formDate, 1)}
                formdate={day.formDate}
                cellRef={cellRef}
                cellH={cellH}
                intervalRented={intervalRented}
                // pos={pos}
              />
            ) : null}
          </div>
        );
      });
    })
  );
  const superMonths = useMemo(() => {
    return intervalMonth.slice(0, interval - 1).map((day, i) => {
      return (
        <div className="grid-item-rooms supermonth">
          {day.lastDay ? (
            <div
              className="lastday"
              style={{ width: `${day.width * (w * 2)}px` }}
            >
              <MonthRef2 w={w * 2} label={day.month} interval={interval} />
            </div>
          ) : null}
        </div>
      );
    });
  });
  const superMonths2 = useMemo(() => {
    return intervalMonth.slice(interval - 1, 120).map((day, i) => {
      return (
        <div className="grid-item-rooms supermonth">
          {day.lastDay ? (
            <div
              className="lastday"
              style={{ width: `${day.width * (w * 2)}px` }}
            >
              <MonthRef2 w={w * 2} label={day.month} interval={interval} />
              {/* </div> */}
            </div>
          ) : null}
        </div>
      );
    });
  });

  const monthDays = intervalData.slice(0, interval - 1).map((day) => {
    return (
      <div className="monthdays">
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <div>{day.dayName}</div>
          <div>{day.day}</div>
        </div>
      </div>
    );
  });

  const monthGridStyle = {
    display: "grid",
    gridTemplateColumns: `repeat(${interval - 1}, minmax(0, 1fr))`,
  };
  const weekGridStyle = {
    display: "grid",
    gridTemplateColumns: ` repeat(${interval - 1},minmax(0, 1fr))`,
  };

  const roomsGridStyle = {
    display: "grid",
    gridTemplateColumns: `minmax(0, 0.5fr) repeat(${
      interval - 3
    },minmax(0, 1fr)) minmax(0, 1fr)  minmax(0, 0.5fr) `,

    gridAutoRows: "1fr",
    height: "80vh",
    position: "relative",
    // zIndex: 2,
  };
  const roomsGridStyle2 = {
    width: `${w * 2 * (intervalGrid2 - 1)}px`,
    // zIndex: 2,

    display: "grid",
    gridTemplateColumns: `${w}px  repeat(auto-fill, minmax(${
      w * 2
    }px, 1fr))  ${w}px `,
    gridAutoColumns: `${w * 2}px`,
    position: "relative",
    gridAutoRows: "1fr",
    height: "80vh",
  };
  const supermonth2 = {
    width: `${w * 2 * (intervalGrid2 - 1)}px`,
    position: "absolute",

    display: "grid",
    gridTemplateColumns: `repeat(${intervalGrid2},minmax(${w * 2}px, 1fr)) `,
  };
  const containerGrid = {
    display: "grid",
    gridTemplateColumns: `10% 90% 0fr `,
    gridTemplateRows: `3% 5%  80% 5% 5% `,
    height: "100vh",
    boxSizing: "border-box",
  };

  return (
    <div onMouseMove={clicked ? handleFixPos : handlePos} style={containerGrid}>
      <div className="button-container">
        <Button
          variant="contained"
          color="primary"
          startIcon={<AddIcon />}
          onClick={handleAddReservation}
          className="add-reservation-button"
        ></Button>
        <NavDays />
      </div>

      <RoomNumbers rooms={hotelRooms} />

      <div className="month-container">
        <div style={monthGridStyle} className="month-grid">
          {superMonths}
        </div>
      </div>

      <div className="days-container">
        <div style={weekGridStyle}>{monthDays}</div>
        {/* {id ? <RentedDescription pos={pos} /> : null} */}
      </div>

      <div className="rooms-grid-container">
        <div ref={containerRef} style={roomsGridStyle}>
          {grid}
        </div>
      </div>

      <div className="scrollbar-container">
        <ScrollBar h={cellH} w={w} />
      </div>

      <div
        style={{
          display: "flex",
          justifyContent: "flex-start",
          height: "100vh",
        }}
      >
        <div
          style={{ display: "flex", flexDirection: "column", height: "100%" }}
        >
          <div className="super-month-2">
            <div style={supermonth2}>{superMonths2}</div>
          </div>
          <div className="spacer-1" />
          <div className="spacer-2" />
          <div ref={containerRef2} style={roomsGridStyle2}>
            {grid2}
          </div>
        </div>
      </div>
    </div>
  );
};
