export const getStyleFromJSON = ({ billingStatus, housekeepingStatus }) => {
  const combinedKey = `${billingStatus}-${housekeepingStatus}`;
  const combinedStyle = styleJSON.statusStyles.combined[combinedKey];

  if (combinedStyle) {
    return {
      backgroundColor: combinedStyle.backgroundColor,
      color: "#000000", // Color de texto negro por defecto
      borderColor: "#000000", // Color de borde negro por defecto
    };
  } else {
    // Si no se encuentra una combinacin especfica, devuelve un estilo por defecto
    return {
      backgroundColor: "#CCCCCC", // Gris claro por defecto
      color: "#000000",
      borderColor: "#000000",
    };
  }
};
