import React, { useState } from "react";
import "./App.css";
import { ContainerContainer } from "./components/Container/ContainerContainer";
import { Provider } from "./context/ThumbContext";
import { Provider as DatesProvider } from "./context/DatesContext";
import { Provider as FormProvider } from "./context/FormContext";
import { Provider as IdProvider } from "./context/IdContext";
import { Provider as SocketProvider } from "./context/SocketContext";
import { Provider as ResDataProvider } from "./context/ResDataContext";
import { Provider as RentedProvider } from "./context/RentedFormContext";
import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";
import { DndProvider } from "react-dnd-multi-backend";
import { HTML5Backend } from "react-dnd-html5-backend";
import { TouchBackend } from "react-dnd-touch-backend";
import {
  MultiBackend,
  TouchTransition,
  MouseTransition,
} from "react-dnd-multi-backend";
import LoginForm from "./components/Form/LoginForm";

const HTML5toTouch = {
  backends: [
    {
      id: "html5",
      backend: HTML5Backend,
      preview: true,
      transition: MouseTransition,
    },
    {
      id: "touch",
      backend: TouchBackend,
      options: {
        enableMouseEvents: true,
        delayTouchStart: 100, // Adjust this value for better touch response
      },
      preview: true,
      transition: TouchTransition,
    },
  ],
};

export default function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  return (
    <IdProvider>
      <FormProvider>
        <DatesProvider>
          <SocketProvider>
            <ResDataProvider>
              <RentedProvider>
                <Provider>
                  <DndProvider backend={MultiBackend} options={HTML5toTouch}>
                    <BrowserRouter>
                      <Routes>
                        <Route
                          path="/"
                          element={
                            isLoggedIn ? (
                              <Navigate to="/app" replace />
                            ) : (
                              <LoginForm onLogin={() => setIsLoggedIn(true)} />
                            )
                          }
                        />
                        <Route
                          path="/app"
                          element={
                            isLoggedIn ? (
                              <ContainerContainer />
                            ) : (
                              <Navigate to="/" replace />
                            )
                          }
                        />
                      </Routes>
                    </BrowserRouter>
                  </DndProvider>
                </Provider>
              </RentedProvider>
            </ResDataProvider>
          </SocketProvider>
        </DatesProvider>
      </FormProvider>
    </IdProvider>
  );
}
