import React, { useId, useContext, useState, useRef, useEffect } from "react";
import { Context as IdContext } from "../../context/IdContext";
import { useDrop } from "react-dnd";
import { Context as FormContext } from "../../context/FormContext";
import { Context } from "../../context/DatesContext";
import axios from "axios";
import { addYears, formatWithOptions } from "date-fns/fp";
import { es } from "date-fns/locale";
import { Context as SocketContext } from "../../context/SocketContext";
import { format } from "date-fns";

import {
  isAfter,
  isBefore,
  eachDayOfInterval,
  add,
  addDays,
  subDays,
  differenceInDays,
  isWeekend,
  areIntervalsOverlapping,
} from "date-fns";

import "./Grid.css";

import { Rented } from "./Rented";

export const GridCell = ({
  containerRef,
  reference,
  rented,
  className,
  index,
  w,
  pos,
  onDClick,
  containerWidth,
  containerLeft,
  interval,
  day,
  room,
  cellRef,
  cellH,
}) => {
  const { state, removeByDragMsg } = useContext(Context);
  const { intervalRented, userId } = useContext(FormContext).state;
  const {
    calendar: { intervalRentedArray, intervalData },
  } = state;
  const { socket } = useContext(SocketContext).state;
  const [isHovered, setIsHovered] = useState(false);

  const formatDate = (date) => {
    return format(new Date(date), "d 'de' MMMM, yyyy", { locale: es });
  };

  const [{ isOver }, drop] = useDrop(
    () => ({
      accept: "RENTED",
      hover: (item, monitor) => {},

      drop: async (item, monitor) => {
        const newEnd = isBefore(day, new Date(item.rented.start))
          ? subDays(
              new Date(item.rented.start),
              differenceInDays(new Date(item.rented.end), day)
            )
          : isAfter(day, new Date(item.rented.end))
          ? addDays(
              new Date(item.rented.start),
              differenceInDays(day, new Date(item.rented.end))
            )
          : new Date(item.rented.end);

        const isOverlapping = intervalRentedArray.some((interval) => {
          return (
            areIntervalsOverlapping(
              { start: day, end: newEnd },
              { start: new Date(interval.end), end: new Date(interval.start) }
            ) &&
            interval.room.toString() === room.toString() &&
            interval.id !== item.rented.id
          );
        });

        if (!isOverlapping) {
          // const requestUrl = `http://localhost:8000/update-reservation/${item.rented.id}`;
          const requestUrl = `/update-reservation/${item.rented.id}`;

          try {
            const res = await axios({
              method: "put",
              url: requestUrl,
              data: {
                ...item.rented,
                room: `${room}`,
                start: day,
                userId,
                end: newEnd,
              },
            });
          } catch (error) {
            console.error(error); // Handle error response
          }
        }
      },

      collect: (monitor) => {
        return {
          isOver: monitor.isOver(),
          canDrop: monitor.canDrop(),
        };
      },
    }),
    [[intervalData]]
  );

  const handleTouchStart = (e) => {
    if (rented) {
      let lastTap = 0;
      const currentTime = new Date().getTime();
      const tapLength = currentTime - lastTap;
      if (tapLength < 300 && tapLength > 0) {
        e.preventDefault();
        onDClick(rented);
      }
      lastTap = currentTime;
    }
  };

  return index !== interval - 1 ? (
    <div
      style={{}}
      ref={drop}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <div
        ref={cellRef}
        onDoubleClick={onDClick}
        onTouchStart={handleTouchStart}
        className={`${className} grid-cell`}
        style={
          isOver
            ? { backgroundColor: "red" }
            : {
                backgroundColor: `${
                  isWeekend(day.formDate) ? "green" : "#ffffff"
                }`,
              }
        }
      >
        {isHovered && !rented && (
          <div className="hover-info">
            <span>{room}</span>
            <span> {formatWithOptions({ locale: es }, "/dd/MM")(day)}</span>
          </div>
        )}
        {rented ? (
          <Rented
            cellH={cellH}
            containerRef={containerRef}
            containerLeft={containerLeft}
            containerWidth={containerWidth}
            index={index}
            pos={pos}
            id={rented.id}
            className={"draggable"}
            w={w}
            rented={rented}
          />
        ) : null}
      </div>
    </div>
  ) : (
    <div
      style={
        isOver
          ? { backgroundColor: "blue" }
          : {
              backgroundColor: `${
                isWeekend(day.formDate) ? "#8b2626" : "#ffffff"
              }`,
            }
      }
      ref={reference}
      className="grid2"
    />
  );
};
