import React, { useState, useContext } from "react";
import { InputForm } from "../Form/NewForm";
import { Context as DatesContext } from "../../context/DatesContext";
import { SearchPax } from "../Form/SearchPax";
import { useForm } from "react-hook-form";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import CloseIcon from "@mui/icons-material/Close";
import { Context as FormContext } from "../../context/FormContext";

import "./NavDays.css";

export const NavDays = () => {
  const { intervalRented } = useContext(FormContext).state;
  const { start } = intervalRented;
  const [isOpen, setIsOpen] = useState(false);
  const {
    state: { searchName, selectedRentedId },
    selectedRentedMsg,
    addIntervalMsg,
    searchNameMsg,
  } = useContext(DatesContext);

  const formattedStartDate = start
    ? new Date(start).toISOString().split("T")[0]
    : new Date().toISOString().split("T")[0];

  const { control, watch, setValue } = useForm({
    defaultValues: {
      startInput: formattedStartDate,
      searchName: searchName,
    },
  });

  const handleSearchPaxSelect = (selectedDate, id) => {
    addIntervalMsg("startDate", selectedDate);
    selectedRentedMsg(id);
    setIsOpen(false);
  };

  const toggleForm = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="navDaysContainer">
      <IconButton
        color="primary"
        onClick={toggleForm}
        className="toggleButton"
        size="small"
      >
        {isOpen ? (
          <CloseIcon fontSize="small" />
        ) : (
          <MenuIcon fontSize="small" />
        )}
      </IconButton>

      {isOpen && (
        <div className="navDaysForm open">
          <div className="formContent">
            <IconButton
              color="primary"
              onClick={toggleForm}
              className="closeButton"
              size="small"
            >
              <CloseIcon fontSize="small" />
            </IconButton>
            <SearchPax onSelectPax={handleSearchPaxSelect} />
            <InputForm
              control={control}
              name="startInput"
              type="date"
              label="Desde"
              value={watch("startInput")}
              handleChange={(newDate) => {
                setValue("startInput", newDate);
                addIntervalMsg("startDate", new Date(newDate));
              }}
            />
          </div>
        </div>
      )}
    </div>
  );
};
