import React, { useContext, useEffect, useRef, useState } from "react";

import { Grid } from "../Grid/Grid";
import { EditForm } from "../Form/EditForm";
import { Context } from "../../context/DatesContext";
import { Context as FormContext } from "../../context/FormContext";
import { Context as RentedContext } from "../../context/RentedFormContext";
import { Context as SocketContext } from "../../context/SocketContext";
import { NavDays } from "../NavDays/NavDays";
import axios from "axios";
import { getStyleFromJSON } from "../../utils/stylesUtils";

// import "./FormContainerStyle.css"; // Adjust the path as necessary
import { NewFormm } from "../Form/NewFormm";
export const Container = () => {
  const containerRef = useRef();
  const {
    loadIntervals_db_msg,
    addIntervalToArrayRentedMsg,
    deleteMsg,
    modifyIntervalToArrayRentedMsg,
  } = useContext(Context);
  const { userId, intervalRented } = useContext(FormContext).state;
  const { rented } = useContext(RentedContext).state;
  const { showEditForm } = rented;
  const { showForm } = intervalRented;
  const { socket } = useContext(SocketContext).state;

  const getRentedArray = async () => {
    try {
      // const res = await axios.get(`http://localhost:8000/all`, {
      const res = await axios.get(`/all`, {
        params: {
          userId: userId,
        },
      });
    } catch (error) {
      console.error("Error fetching reservations:", error);
    }
  };
  const [totalSales, setTotalSales] = useState(null);
  const [excel, setExcel] = useState(null);
  const [paymentMethodTotals, setPaymentMethodTotals] = useState(null);
  const fetchPaymentMethodTotals = async () => {
    const currentDate = new Date();
    const currentMonth = currentDate.getMonth() + 1; // getMonth() returns 0-11

    try {
      const response = await axios.get(
        // `http://localhost:8000/payment-method-totals/${currentMonth}`,
        `/payment-method-totals/${currentMonth}`,

        {
          params: { userId: userId }, // Make sure userId is defined in your component
        }
      );

      setPaymentMethodTotals(response.data.totals);
    } catch (error) {}
  };
  const fetchTotalSales = async () => {
    const currentDate = new Date();
    const currentMonth = currentDate.getMonth() + 1; // getMonth() returns 0-11
    const currentYear = currentDate.getFullYear();

    try {
      const response =
        // await axios.get`http://localhost:8000/total-sales/${currentMonth}/`;

        await axios.get(`/total-sales/${currentMonth}/`);

      setTotalSales(response.data.totalSales);
    } catch (error) {}
  };
  useEffect(() => {
    socket.emit("login", userId);
    getRentedArray();
  }, []);

  useEffect(() => {
    socket.on("allReservations", (msg) => {
      loadIntervals_db_msg(msg.userReservations);
    });
  });

  useEffect(() => {
    socket.on("reservationCreated", (msg) => {
      console.log(msg);
      const formattedReservations = msg.map((msg) => ({
        ...msg,
        start: new Date(msg.start),
        end: new Date(msg.end),
        update: true,
        style: getStyleFromJSON({
          billingStatus: msg.billingStatus,
          housekeepingStatus: msg.housekeepingStatus,
        }),
      }));
      addIntervalToArrayRentedMsg(formattedReservations);
    });
  }, [userId]);

  useEffect(() => {
    socket.on(
      "updateReservation",
      (msg) => {
        if (msg.user === userId && !msg.isOverLappingg) {
          const updatedReservation = {
            ...msg,
            start: new Date(msg.start),
            end: new Date(msg.end),
            update: true,
            style: getStyleFromJSON({
              billingStatus: msg.billingStatus,
              housekeepingStatus: msg.housekeepingStatus,
            }),
          };
          modifyIntervalToArrayRentedMsg(updatedReservation);
        }
      },
      [userId]
    );

    socket.on("deleteReservation", (msg) => {
      deleteMsg(msg);
    });

    return () => {
      socket.off("updateReservation");
      socket.off("deleteReservation");
    };
  }, [userId]);
  const fetchExcel = async () => {
    // fetch("http://localhost:8000/export-excel")
    fetch("/export-excel")
      .then((response) => {
        if (response.ok) {
          return response.blob();
        } else {
          throw new Error("Error al exportar el Excel");
        }
      })
      .then((blob) => {
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = url;
        a.download = "reservas.xlsx";
        document.body.appendChild(a);
        a.click();
        a.remove();
      })
      .catch((error) => {
        console.error(error);
      });
  };
  return (
    // <div style={{ height: "200px", backgroundColor: "blue" }}>
    <div ref={containerRef}>
      {/* <div
        style={{
          backgroundColor: "lightgray",
          padding: "10px",
          // marginBottom: "10px",
        }}
      ></div> */}
      <Grid />

      {/* <h1 className="">Hello world!</h1> */}
      <div className="totalSales">
        <button
          variant="contained"
          color="primary"
          onClick={fetchTotalSales}
          style={{ marginTop: "20px" }}
        >
          Fetch Total Sales for Current Month
        </button>
        {totalSales !== null && (
          <p style={{ marginTop: "10px" }}>
            Total Sales for Current Month: ${totalSales.toFixed(2)}
          </p>
        )}
      </div>
      <div className="totalSales">
        <button
          variant="contained"
          color="primary"
          onClick={fetchExcel}
          style={{ marginTop: "20px" }}
        >
          Fetch Excel
        </button>
        {/* {totalSales !== null && (
          <p style={{ marginTop: "10px" }}>
            Total Sales for Current Month: ${totalSales.toFixed(2)}
          </p>
        )} */}
      </div>

      <div className="paymentMethodTotals">
        <button
          variant="contained"
          color="primary"
          onClick={fetchPaymentMethodTotals}
          style={{ marginTop: "20px" }}
        >
          Fetch Payment Method Totals for Current Month
        </button>
        {paymentMethodTotals !== null && (
          <div style={{ marginTop: "10px" }}>
            <p>Payment Method Totals for Current Month:</p>
            <p>Efectivo: ${paymentMethodTotals.efectivo.toFixed(2)}</p>
            <p>Tarjeta: ${paymentMethodTotals.tarjeta.toFixed(2)}</p>
            <p>Deposito: ${paymentMethodTotals.deposito.toFixed(2)}</p>
          </div>
        )}
      </div>
      {showEditForm ? (
        <EditForm />
      ) : showForm ? (
        <NewFormm
          reservation={intervalRented.formType ? intervalRented : null}
        />
      ) : null}
    </div>
    // </div>
  );
};
