import React from "react";
import TextField from "@mui/material/TextField";
import MenuItem from "@mui/material/MenuItem";

import { useController } from "react-hook-form";
import Button from "@mui/material/Button";
import ButtonGroup from "@mui/material/ButtonGroup";
import { InputAdornment } from "@mui/material";
import "./InputForm.css";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import Select from "@mui/material/Select";
import FormHelperText from "@mui/material/FormHelperText";
// import MenuItem from "@mui/material/MenuItem";

export const InputForm = ({
  control,
  name,
  value,
  type,
  select,
  options,
  rules,
  handleChange,
  className,
  multiline,
  rows,
  style,
  label,
  onGuestInfoClick,
  multiple,
}) => {
  const { field, fieldState } = useController({
    name,
    control,
    rules: { required: true, ...rules },
  });
  if (type === "select") {
    return (
      <div className={className}>
        <FormControl fullWidth margin="normal">
          <InputLabel id={`${name}-label`} style={{ whiteSpace: "normal" }}>
            {label}
          </InputLabel>
          <Select
            {...field}
            labelId={`${name}-label`}
            value={value}
            onChange={(e) => {
              field.onChange(e.target.value);
              handleChange(e.target.value);
            }}
            error={!!fieldState.error}
            multiple={multiple}
            style={{ width: "100%" }}
          >
            {options.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </Select>
          {fieldState.error && (
            <FormHelperText>{fieldState.error.message}</FormHelperText>
          )}
        </FormControl>
      </div>
    );
  }
  if (name === "paymentMethod" || name === "roomType") {
    return (
      <div className={className}>
        <label>{label}</label>
        <ButtonGroup variant="contained" aria-label={`${name} selection`}>
          {options.map((option) => (
            <Button
              key={option.value}
              onClick={() => {
                field.onChange(option.value);
                handleChange(option.value);
              }}
              variant={value === option.value ? "contained" : "outlined"}
            >
              {option.label}
            </Button>
          ))}
        </ButtonGroup>
        {fieldState.error && (
          <p className="errorMessage">{fieldState.error.message}</p>
        )}
      </div>
    );
  }
  if (name === "numberOfGuests") {
    return (
      <div className={className}>
        <TextField
          {...field}
          label={label}
          type="number"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <Button onClick={onGuestInfoClick}>+</Button>
              </InputAdornment>
            ),
          }}
          onChange={(e) => {
            field.onChange(e);
            handleChange(e.target.value);
          }}
          error={!!fieldState.error}
          helperText={fieldState.error?.message}
        />
      </div>
    );
  }
  if (type === "textarea") {
    return (
      <div className={className} style={style}>
        <label htmlFor={name}>{label}</label>
        <textarea
          id={name}
          name={name}
          value={value}
          className={className}
          onChange={(e) => {
            field.onChange(e.target.value);
            handleChange(e.target.value);
          }}
          rows={rows}
          style={{ width: "100%" }}
        />
        {fieldState.error && (
          <p className="errorMessage">{fieldState.error.message}</p>
        )}
      </div>
    );
  }
  if (type === "date") {
    return (
      <div className={className} style={style}>
        <label htmlFor={name}>{label}</label>
        <input
          id={name}
          name={name}
          type="date"
          value={value}
          onChange={(e) => {
            field.onChange(e.target.value);
            handleChange(e.target.value);
          }}
          style={{ width: "100%" }}
        />
        {fieldState.error && (
          <p className="errorMessage">{fieldState.error.message}</p>
        )}
      </div>
    );
  }
  if (type === "number") {
    return (
      <div className={className} style={style}>
        {/* <label htmlFor={name}>{label}</label> */}
        <TextField
          inputProps={{ type, className: "dateInput" }}
          onChange={(e) => {
            field.onChange(e.target.value);
            handleChange(e.target.value);
          }}
          onBlur={field.onBlur}
          style={style}
          value={value}
          name={field.name}
          multiline={multiline}
          rows={rows}
          // className={className}
          type={type}
          inputRef={field.ref}
          label={label}
          rules={rules}
        />
        {fieldState.error && (
          <p className="errorMessage">{fieldState.error.message}</p>
        )}
      </div>
    );
  }
  // Rest of the component remains the same
  return (
    <div className="input-container">
      {select ? (
        <TextField
          select
          value={value}
          onChange={(e) => {
            field.onChange(e.target.value);
            handleChange(e.target.value);
          }}
          label={label}
          helperText={fieldState.error ? fieldState.error.message : null}
        >
          {options.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
      ) : (
        <TextField
          inputProps={{ type, className: "dateInput" }}
          onChange={(e) => {
            field.onChange(e.target.value);
            handleChange(e.target.value);
          }}
          onBlur={field.onBlur}
          style={style}
          value={value}
          name={field.name}
          multiline={multiline}
          rows={rows}
          className={className}
          type={type}
          inputRef={field.ref}
          label={label}
          rules={rules}
        />
      )}
      <div className="errorContainer">
        {fieldState.error && (
          <p className="errorMessage">{fieldState.error.message}</p>
        )}
      </div>
    </div>
  );
};
