import React, {
  useState,
  useEffect,
  useContext,
  useRef,
  useCallback,
} from "react";
import { Context as IdContext } from "../../context/IdContext";
import { Context as DatesContext } from "../../context/DatesContext";
import { RentedDescription } from "./RentedDescription";
import { Context as FormContext } from "../../context/FormContext";
import { useDrag } from "react-dnd";
import { Context as RentedContext } from "../../context/RentedFormContext";
import styleJSON from "./statusStyles.json";
const defaultStyle = {
  backgroundColor: "rgba(189, 195, 199, 0.8)",
  borderColor: "#7f8c8d",
  boxShadow: "0 4px 6px rgba(189, 195, 199, 0.3)",
  color: "black",
};
// const getStyleFromJSON = (rented) => {
//   // Por ahora, solo usaremos un estilo bsico
//   return styleJSON.statusStyles.billing.pendiente;
// };
export const Rented = React.memo(
  ({
    w,
    className,
    rented,
    index,
    containerWidth,
    containerLeft,
    containerWidth2,
    containerLeft2,
    dragRef,
    containerRef,
    onmouseDownHandler,
    intervalRented,
    inView,
    cellH,
    ...props
  }) => {
    const style = rented.style || defaultStyle;
    const [showDescription, setShowDescription] = useState(false);
    const [descriptionPos, setDescriptionPos] = useState({ x: 0, y: 0 });
    const [width, setWidth] = useState(0);
    const [left, setLeft] = useState(0);
    const rentedRef = useRef(null);
    const [isRSMHovering, setIsRSMHovering] = useState(false);

    const { rentedMsg } = useContext(RentedContext);
    const { formSubmittedMsg } = useContext(FormContext);
    const { idMsg } = useContext(IdContext);
    const { clicked_Msg } = useContext(DatesContext);

    const [{ isDragging }, drag] = useDrag(() => ({
      type: "RENTED",
      item: {
        rented,
        begin: () => {
          idMsg(false);
          setShowDescription(false);
          setDescriptionPos({ x: 0, y: 0 });
        },
      },
      end: () => {
        formSubmittedMsg(true);
      },
      collect: (monitor) => ({
        isDragging: !!monitor.isDragging(),
      }),
    }));

    useEffect(() => {
      if (rentedRef.current !== null) {
        const RR = rentedRef.current.getBoundingClientRect();
        const left = RR.left - containerLeft;
        setLeft(left);
        setWidth(containerWidth - left);
      }
    }, [containerLeft, containerWidth]);

    const handleMouseEnter = useCallback(
      (e) => {
        if (!isRSMHovering) {
          setShowDescription(true);
          idMsg(rented);
          updateDescriptionPosition(e);
        }
      },
      [isRSMHovering, rented, idMsg]
    );

    const handleMouseLeave = useCallback(() => {
      setShowDescription(false);
      idMsg(false);
    }, [idMsg]);

    const handleClick = useCallback(
      (e) => {
        e.stopPropagation();
        setShowDescription(true);
        clicked_Msg(true);
        idMsg(rented);
        updateDescriptionPosition(e);
      },
      [clicked_Msg, idMsg, rented]
    );

    const DclickHandle = useCallback(
      (e) => {
        e.preventDefault();
        e.stopPropagation();
        rentedMsg({
          ...rented,
          showEditForm: true,
          start: new Date(rented.start),
          end: new Date(rented.end),
        });
      },
      [rentedMsg, rented]
    );

    const closeDescription = useCallback(() => {
      setShowDescription(false);
      idMsg(false);
    }, [idMsg]);

    const updateDescriptionPosition = useCallback((e) => {
      setDescriptionPos({
        x: e.clientX,
        y: e.clientY,
      });
    }, []);

    const triangleHeight = cellH / 2.5;
    const totalHeight = triangleHeight * 2;
    // const style = getStyleFromJSON({
    //   billingStatus: rented.billingStatus,
    //   housekeepingStatus: rented.housekeepingStatus
    // });

    return (
      <div
        className="rented-main-container"
        onDoubleClick={DclickHandle}
        ref={drag}
        style={{
          display: "flex",
          boxSizing: "border-box",
          height: `${totalHeight}px`,
          position: "relative",
        }}
      >
        <div
          className="triangle-left"
          style={{
            width: 0,
            height: 0,
            borderTop: `${triangleHeight}px solid transparent`,
            borderRight: `${w / 4.5}px solid ${style.backgroundColor}`,
            borderBottom: `${triangleHeight}px solid transparent`,
            filter: `drop-shadow(0 1px 2px ${style.boxShadow})`,
          }}
        />

        <div
          ref={rentedRef}
          className={`${rented.className} rented-content`}
          style={{
            display: "flex",
            justifyContent: "flex-start",
            cursor: "pointer",
            width: `${(rented.width / 100) * (w * 2) - w / 2}px`,
            backgroundColor: style.backgroundColor,
            boxShadow: style.boxShadow,
            borderColor: style.borderColor,
          }}
        >
          <div style={{}}></div>
          <div
            style={{
              width: `${containerWidth - 40}px`,
              textAlign: "center",
              overflowX: "hidden",
            }}
          >
            <div
              style={{
                paddingLeft: `${containerLeft <= 0 ? -containerLeft : 0}px`,
                boxSizing: "border-box",
                fontSize: "11px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                color: style.color,
                textShadow: "0 1px 1px rgba(0,0,0,0.1)",
                height: "100%",
              }}
            >
              <div
                style={{
                  height: cellH,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
                onClick={handleClick}
                onMouseEnter={handleMouseEnter}
                onMouseLeave={handleMouseLeave}
              >
                {rented.name}-{rented.surname}
              </div>
            </div>
          </div>
        </div>

        <div
          className="triangle-right"
          style={{
            width: 0,
            height: 0,
            borderTop: `${triangleHeight}px solid transparent`,
            borderLeft: `${w / 4.5}px solid ${style.backgroundColor}`,
            borderBottom: `${triangleHeight}px solid transparent`,
            filter: `drop-shadow(0 1px 2px ${style.boxShadow})`,
          }}
        />

        {showDescription && !isRSMHovering && (
          <RentedDescription
            pos={descriptionPos}
            rented={rented}
            onClose={closeDescription}
          />
        )}
      </div>
    );
  }
);
