import React from "react";
import styled from "@emotion/styled";
import { keyframes } from "@emotion/react";

const fadeIn = keyframes`
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
`;

const Container = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 10px;
  width: 100%;
  height: 280px;
  padding: 10px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  animation: ${fadeIn} 0.3s ease-out;
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
`;

const RightColumn = styled(Column)`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
`;

const InfoItem = styled.p`
  margin: 3px 0;
  font-size: 0.9em;
  color: #333;
`;

const InfoLabel = styled.span`
  font-size: 0.8em;
  color: #777;
  font-weight: bold;
`;

const GuestList = styled.ul`
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-size: 0.8em;
`;

const CommentSection = styled.div`
  grid-column: 1 / span 2;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
`;

const CommentArea = styled.div`
  font-size: 0.8em;
  color: #333;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  height: 80px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  &::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
`;

export const DescDetail = ({
  name,
  phone,
  email,
  dni,
  room,
  roomType,
  start,
  end,
  time,
  nights,
  numberOfGuests,
  guestNames,
  comments,
  price,
  precioTotal,
  paymentMethod,
  isBooking,
  surname,
}) => {
  return (
    <Container>
      <Column>
        <InfoItem>
          <InfoLabel>Nombre:</InfoLabel> {name}
        </InfoItem>
        <InfoItem>
          <InfoLabel>Apellido:</InfoLabel> {surname}
        </InfoItem>
        <InfoItem>
          <InfoLabel>Telfono:</InfoLabel> {phone}
        </InfoItem>
        <InfoItem>
          <InfoLabel>Email:</InfoLabel> {email}
        </InfoItem>
        <InfoItem>
          <InfoLabel>DNI:</InfoLabel> {dni}
        </InfoItem>
        <InfoItem>
          <InfoLabel>N Huspedes:</InfoLabel> {numberOfGuests}
        </InfoItem>
        <InfoItem>
          <InfoLabel>Huspedes:</InfoLabel>
          <GuestList>
            {Array.isArray(guestNames) &&
              guestNames.map((guest, index) => <li key={index}>{guest}</li>)}
          </GuestList>
        </InfoItem>
      </Column>

      <RightColumn>
        <Column>
          <InfoItem>
            <InfoLabel>Habitacin:</InfoLabel> {room} {roomType}
          </InfoItem>
          <InfoItem>
            <InfoLabel>Ingreso:</InfoLabel> {start}
          </InfoItem>
          <InfoItem>
            <InfoLabel>Egreso:</InfoLabel> {end} {nights}
          </InfoItem>
          <InfoItem>
            <InfoLabel>Hora:</InfoLabel> {time}
          </InfoItem>
        </Column>

        <Column>
          <InfoItem>
            <InfoLabel>Booking:</InfoLabel> {isBooking ? "S" : "No"}
          </InfoItem>
          <InfoItem>
            <InfoLabel>Precio/Noche:</InfoLabel> {price}
          </InfoItem>
          <InfoItem>
            <InfoLabel>Total:</InfoLabel> {precioTotal}
          </InfoItem>
          <InfoItem>
            <InfoLabel>Mtodo de pago:</InfoLabel> {paymentMethod}
          </InfoItem>
        </Column>

        <CommentSection>
          <InfoLabel>Comentarios:</InfoLabel>
          <CommentArea>{comments}</CommentArea>
        </CommentSection>
      </RightColumn>
    </Container>
  );
};
