import createDataContext from "./createDataContext";
import { addYears, formatWithOptions, format } from "date-fns/fp";
import { es } from "date-fns/locale";
import { u } from "react-uuid";
import { getStyleFromJSON } from "../utils/stylesUtils";
import {
  eachDayOfInterval,
  add,
  addDays,
  subDays,
  differenceInDays,
  getDaysInMonth,
  isFirstDayOfMonth,
  isLastDayOfMonth,
} from "date-fns";
import _ from "lodash";

const MSGS = {
  ADD_DAY: "ADD_DAY",
  SUB_DAY: "SUB_DAY",
  NEW_DAY: "NEW_DAY",
  NEW_INTERVAL_RENTED: "NEW_INTERVAL_RENTED",
  INTERVAL_RENTED_DB: "INTERVAL_RENTED_DB",
  START_DATE: "START_DATE",
  SEARCH_NAME: "SEARCH_NAME",
  REMOVE_BY_DRAGGING: "REMOVE_BY_DRAGGING",
  SEND_INTERVAL_FOR_SOCKET: "SEND_INTERVAL_FOR_SOCKET",
  EDITING: "EDITING",
  DELETE_INTERVAL_RENTED: "DELETE_INTERVAL_RENTED",
  SEARCH_ID: "SEARCH_ID",
  POS: "POS",
  CLICKED_RENTED: "CLICKED_RENTED",
  UPDATE_RENTED_STATUS: "UPDATE_RENTED_STATUS",
};

const updateRentedStatusMsg =
  (dispatch) => (id, billingStatus, housekeepingStatus) => {
    console.log(id, billingStatus, housekeepingStatus);
    dispatch({
      type: MSGS.UPDATE_RENTED_STATUS,
      id,
      billingStatus,
      housekeepingStatus,
    });
  };
const clicked_Msg = (dispatch) => (clicked) => {
  dispatch({
    type: MSGS.CLICKED_RENTED,
    clicked,
  });
};
const pos_Msg = (dispatch) => (pos) => {
  dispatch({
    type: MSGS.POS,
    pos,
  });
};
const deleteMsg = (dispatch) => (intervalRentedId) => {
  dispatch({
    type: MSGS.DELETE_INTERVAL_RENTED,
    intervalRentedId,
  });
};
const selectedRentedMsg = (dispatch) => (id) => {
  console.log(id);
  dispatch({
    type: MSGS.SEARCH_ID,
    id,
  });
};
const editingMsg = (dispatch) => (intervalRented) => {
  dispatch({
    type: MSGS.EDITING,
    intervalRented,
  });
};

const removeByDragMsg =
  (dispatch) =>
  ({ price, nights, width, email, phone, name, id, start, room, end }) => {
    dispatch({
      type: MSGS.REMOVE_BY_DRAGGING,
      id,
      start,
      room: room - 1,
      email,
      phone,
      name,
      end,
      width,
      nights,
      price,
    });
  };
const sendIntervalForSocketMsg = (dispatch) => (intervalRented) => {
  dispatch({
    type: MSGS.SEND_INTERVAL_FOR_SOCKET,
    intervalRented,
  });
};

const searchNameMsg = (dispatch) => (searchTerm) => {
  dispatch({
    type: MSGS.SEARCH_NAME,
    searchTerm,
  });
};
const loadIntervals_db_msg = (dispatch) => (intervalsArray_db) => {
  dispatch({
    type: MSGS.INTERVAL_RENTED_DB,
    intervalsArray_db,
  });
};
const newDayMsg = (dispatch) => (day) => {
  dispatch({
    type: MSGS.NEW_DAY,
    day,
  });
};

const addIntervalMsg = (dispatch) => (intervalSign, startDate) => {
  dispatch({
    type: MSGS.ADD_DAY,
    intervalSign,
    startDate,
  });
};
const addIntervalToArrayRentedMsg = (dispatch) => (reservations) => {
  dispatch({
    type: MSGS.NEW_INTERVAL_RENTED,
    reservations,
  });
};
const modifyIntervalToArrayRentedMsg = (dispatch) => (intervalRented) => {
  dispatch({
    type: MSGS.EDITING,
    intervalRented,
  });
};

const DatesContext = (state, msg) => {
  switch (msg.type) {
    case MSGS.UPDATE_RENTED_STATUS: {
      const { id, billingStatus, housekeepingStatus } = msg;
      console.log(billingStatus);
      const {
        calendar: { intervalRentedArray },
      } = state;

      const newIntervalRentedArray = intervalRentedArray.map((interval) => {
        if (interval.id === id) {
          return { ...interval, billingStatus, housekeepingStatus };
        }
        return interval;
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }
    case MSGS.REMOVE_BY_DRAGGING: {
      const { nights, width, email, phone, name, id, start, room, end, price } =
        msg;

      const {
        calendar: { intervalRentedArray },
      } = state;

      const newIntervalRented = {
        end: formatWithOptions({ locale: es }, "yyyy/MM/dd")(end),
        start: formatWithOptions({ locale: es }, "yyyy/MM/dd")(start),
        room,
        name,
        id,
        email,
        phone,
        nights,
        price,
        width,
        className: "draggable",
      };

      const newIntervalRentedArray = intervalRentedArray.map((interval) => {
        return interval.id === newIntervalRented.id
          ? newIntervalRented
          : interval;
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,

          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }

    case MSGS.SEARCH_NAME: {
      const { searchTerm } = msg;
      const {
        calendar: { intervalRentedArray },
      } = state;
      //   const cleanIdArray = searchedIds.filter((v, i, a) => a.indexOf(v) === i);
      const searchedName = intervalRentedArray.filter((interval) => {
        return interval.name === searchTerm;
      });
      const ids = searchedName
        .map((interval) => interval.id)
        .filter((v, i, a) => a.indexOf(v) === i);
      console.log(ids);
      const performSearch = (searchTerm, intervalRentedArray) => {
        return intervalRentedArray.filter(
          (rented) =>
            rented.name.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
            (rented.guestNames &&
              rented.guestNames.some((guestName) =>
                guestName.toLowerCase().startsWith(searchTerm.toLowerCase())
              ))
        );
      };
      const newIntervalRentedArray = intervalRentedArray.map((interval) => {
        return ids.find((id) => id === interval.id)
          ? { ...interval, className: "searched" }
          : { ...interval, className: "draggable" };
      });
      const results = performSearch(searchTerm, intervalRentedArray);
      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRentedArray: newIntervalRentedArray,
        },
        searchTerm,
        searchedIds: ids,
        searchResults: results,
      };
    }

    case MSGS.START_DATE: {
      const { startDate } = msg;
      return { ...state, startDate };
    }
    case MSGS.POS: {
      const { pos } = msg;
      console.log(pos);
      return { ...state, pos };
    }
    case MSGS.CLICKED_RENTED: {
      const { clicked } = msg;

      return { ...state, clicked };
    }
    case MSGS.SEARCH_ID: {
      const { id } = msg;
      return { ...state, selectedRentedId: id };
    }
    case MSGS.SEND_INTERVAL_FOR_SOCKET: {
      const { intervalRented } = msg;

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRented,
        },
      };
    }
    case MSGS.SEND_INTERVAL_FOR_SOCKET: {
      const { intervalRented } = msg;

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRented,
        },
      };
    }

    case MSGS.INTERVAL_RENTED_DB: {
      const { intervalsArray_db } = msg;

      const {
        calendar: { intervalRentedArray },
      } = state;

      const newIntervalRentedArray_db = intervalsArray_db.map((interval) => {
        // const startDate = addDays(new Date(interval.start), 1);
        // const endDate = addDays(new Date(interval.end), 1);
        const formattedDateStart = new Date(interval.start)
          .toISOString()
          .slice(0, 10)
          .replace(/-/g, "/");
        const formattedDateEnd = new Date(interval.end)
          .toISOString()
          .slice(0, 10)
          .replace(/-/g, "/");

        return {
          // start: formatWithOptions({ locale: es }, "yyyy/MM/dd")(interval.end),
          // end: formatWithOptions({ locale: es }, "yyyy/MM/dd")(interval.start),
          start: formattedDateEnd,
          end: formattedDateStart,
          room: interval.room,
          id: interval._id,
          name: interval.name,
          email: interval.email,
          phone: interval.phone,
          nights: interval.nights,
          price: interval.price,
          width:
            differenceInDays(new Date(interval.end), new Date(interval.start)) *
            100,
          className: "draggable",
          adelanto: interval.adelanto,
          comments: interval.comments,
          dni: interval.dni,
          montoPendiente: interval.montoPendiente,
          nombre_recepcionista: interval.nombre_recepcionista,
          precioTotal: interval.precioTotal,
          time: interval.time,
          numberOfGuests: interval.numberOfGuests,
          guestNames: interval.guestNames,
          roomType: interval.roomType,
          paymentMethod: interval.paymentMethod,
          surname: interval.surname,
          isBooking: interval.isBooking,
          billingStatus: interval.billingStatus,
          housekeepingStatus: interval.housekeepingStatus,
        };
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,

          intervalRentedArray: newIntervalRentedArray_db,
        },
      };
    }

    // case MSGS.NEW_INTERVAL_RENTED: {
    //   const { intervalRented } = msg;

    //   const {
    //     calendar: { intervalRentedArray },
    //   } = state;

    //   const formattedDateStart = new Date(intervalRented.start)
    //     .toISOString()
    //     .slice(0, 10)
    //     .replace(/-/g, "/");
    //   const formattedDateEnd = new Date(intervalRented.end)
    //     .toISOString()
    //     .slice(0, 10)
    //     .replace(/-/g, "/");

    //   const {
    //     adelanto,
    //     comments,
    //     dni,
    //     email,
    //     montoPendiente,
    //     name,
    //     room,
    //     id,
    //     time,
    //     nights,
    //     nombre_recepcionista,
    //     phone,
    //     precioTotal,
    //     price,
    //     paymentMethod,
    //     numberOfGuests,
    //     guestNames,
    //     roomType,
    //     isBooking,
    //     surname,
    //     billingStatus,
    //     housekeepingStatus,
    //   } = intervalRented;

    //   const newIntervalRented = {
    //     start: formattedDateEnd,
    //     end: formattedDateStart,
    //     adelanto,
    //     comments,
    //     dni,
    //     email,
    //     montoPendiente,
    //     name,
    //     nights,
    //     id,
    //     nombre_recepcionista,
    //     phone,
    //     time,
    //     precioTotal,
    //     room,
    //     width: differenceInDays(intervalRented.end, intervalRented.start) * 100,
    //     className: "draggable",
    //     price,
    //     paymentMethod,
    //     numberOfGuests,
    //     guestNames,
    //     roomType,
    //     isBooking,
    //     surname,
    //     billingStatus,
    //     housekeepingStatus,
    //   };

    //   const newIntervalRentedArray = [
    //     ...intervalRentedArray,
    //     newIntervalRented,
    //   ];

    //   return {
    //     ...state,
    //     calendar: {
    //       ...state.calendar,
    //       intervalRented: newIntervalRented,
    //       intervalRentedArray: newIntervalRentedArray,
    //     },
    //   };
    // }
    case MSGS.NEW_INTERVAL_RENTED: {
      const { reservations } = msg; // Ahora recibimos un array de reservaciones
      console.log(reservations);
      const {
        calendar: { intervalRentedArray },
      } = state;

      const newIntervalRented = reservations.map((reservation) => {
        const formattedDateStart = new Date(reservation.start)
          .toISOString()
          .slice(0, 10)
          .replace(/-/g, "/");
        const formattedDateEnd = new Date(reservation.end)
          .toISOString()
          .slice(0, 10)
          .replace(/-/g, "/");
        const newStyle = getStyleFromJSON({
          billingStatus: reservation.billingStatus,
          housekeepingStatus: reservation.housekeepingStatus,
        });

        return {
          start: formattedDateEnd,
          end: formattedDateStart,
          adelanto: reservation.adelanto,
          comments: reservation.comments,
          dni: reservation.dni,
          email: reservation.email,
          montoPendiente: reservation.montoPendiente,
          name: reservation.name,
          nights: reservation.nights,
          id: reservation._id,
          nombre_recepcionista: reservation.nombre_recepcionista,
          phone: reservation.phone,
          time: reservation.time,
          precioTotal: reservation.precioTotal,
          room: reservation.room,
          width:
            differenceInDays(
              new Date(reservation.end),
              new Date(reservation.start)
            ) * 100,
          className: "draggable",
          price: reservation.price,
          paymentMethod: reservation.paymentMethod,
          numberOfGuests: reservation.numberOfGuests,
          guestNames: reservation.guestNames,
          roomType: reservation.roomType,
          isBooking: reservation.isBooking,
          surname: reservation.surname,
          billingStatus: reservation.billingStatus,
          housekeepingStatus: reservation.housekeepingStatus,
          style: newStyle,
        };
      });

      const newIntervalRentedArray = [
        ...intervalRentedArray,
        ...newIntervalRented,
      ];

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRented: newIntervalRented[0], // Set the first room as the current intervalRented
          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }
    case MSGS.EDITING: {
      const { intervalRented } = msg;
      const {
        calendar: { intervalRentedArray },
      } = state;
      const formattedDateStart = new Date(intervalRented.start)
        .toISOString()
        .slice(0, 10)
        .replace(/-/g, "/");
      const formattedDateEnd = new Date(intervalRented.end)
        .toISOString()
        .slice(0, 10)
        .replace(/-/g, "/");
      const newStyle = getStyleFromJSON({
        billingStatus: intervalRented.billingStatus,
        housekeepingStatus: intervalRented.housekeepingStatus,
      });
      const {
        adelanto,
        comments,
        dni,
        email,
        montoPendiente,
        name,
        room,
        id,
        time,
        nights,
        nombre_recepcionista,
        phone,
        precioTotal,
        price,
        paymentMethod,
        numberOfGuests,
        guestNames,
        roomType,
        isBooking,
        surname,
        billingStatus,
        housekeepingStatus,
      } = intervalRented;
      const newIntervalRented = {
        start: formattedDateEnd,
        end: formattedDateStart,
        adelanto,
        comments,
        dni,
        email,
        montoPendiente,
        name,
        nights,
        id,
        nombre_recepcionista,
        phone,
        time,
        precioTotal,
        room,
        width: differenceInDays(intervalRented.end, intervalRented.start) * 100,
        className: "draggable",
        price,
        paymentMethod,
        numberOfGuests,
        guestNames,
        roomType,
        isBooking,
        surname,
        billingStatus,
        housekeepingStatus,
        style: newStyle,
      };

      const newIntervalRentedArray = intervalRentedArray.map((interval) => {
        return interval.id === newIntervalRented.id
          ? newIntervalRented
          : interval;
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRented: newIntervalRented,
          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }
    case MSGS.DELETE_INTERVAL_RENTED: {
      const { intervalRentedId } = msg;
      const {
        calendar: { intervalRentedArray },
      } = state;

      const newIntervalRentedArray = intervalRentedArray.filter(
        (interval) => interval.id !== intervalRentedId.id
      );

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }

    case MSGS.ADD_DAY: {
      const { newIntervalSign } = msg;
      const {
        calendar: {
          intervalData,
          generalDatesInterval,
          intervalRentedArray,
          intervalSignFront,
          counter,
        },
        hotelRooms,
      } = state;

      const { intervalSign, startDate } = msg;

      const start =
        intervalSign === "+"
          ? addDays(generalDatesInterval.start, 1)
          : intervalSign === "startDate"
          ? startDate
          : subDays(generalDatesInterval.start, 1);

      const end =
        intervalSign === "+"
          ? addDays(generalDatesInterval.end, 1)
          : subDays(generalDatesInterval.end, 1);

      const newGeneralDatesInterval = { start, end };

      const newIntervalData = eachDayOfInterval(newGeneralDatesInterval).map(
        (day) => {
          return {
            formDate: day,
            day: formatWithOptions({ locale: es }, "d")(day),
            dayName: formatWithOptions({ locale: es }, "EE")(day),
            date: formatWithOptions({ locale: es }, "yyyy/MM/dd")(day),
            month: formatWithOptions({ locale: es }, "MMMM")(day),
          };
        }
      );
      const newIntervalMonth = eachDayOfInterval(newGeneralDatesInterval).map(
        (day) => ({
          firstDay: isFirstDayOfMonth(day) ? day : null,
          lastDay: isLastDayOfMonth(day) ? day : null,
          monthDate: day,
          date: day,
          month: formatWithOptions({ locale: es }, "MMMM")(day),
          year: formatWithOptions({ locale: es }, "d")(day),
          monthDay: formatWithOptions({ locale: es }, "d")(day),
          width: getDaysInMonth(day),
        })
      );
      const newCounter = intervalSign === "+" ? counter + 1 : counter - 1;
      return {
        ...state,
        calendar: {
          counter: newCounter,
          intervalSignFront: intervalSign,
          intervalMonth: newIntervalMonth,
          intervalData: newIntervalData,
          generalDatesInterval: newGeneralDatesInterval,
          intervalRentedArray,
          startDate,
        },
      };
    }

    default:
      return state;
  }
};
export const { Context, Provider } = createDataContext(
  DatesContext,
  {
    newDayMsg,
    searchNameMsg,
    addIntervalMsg,
    addIntervalToArrayRentedMsg,
    loadIntervals_db_msg,
    removeByDragMsg,
    sendIntervalForSocketMsg,
    editingMsg,
    modifyIntervalToArrayRentedMsg,
    deleteMsg,
    selectedRentedMsg,
    pos_Msg,
    clicked_Msg,
    updateRentedStatusMsg,
  },
  {
    clicked: false,
    pos: { x: 0, y: 0 },
    selectedRentedId: null,
    hotelRooms: _.range(1, 16),
    searchName: "",
    searchedIds: [],
    searchResults: [],
    calendar: {
      generalDatesInterval: {
        start: new Date(),
        end: add(new Date(), { months: 4 }),
      },
      intervalRented: {},
      intervalRentedArray: [],
      startDate: new Date(),
      intervalSignFront: "+",
      intervalData: eachDayOfInterval({
        start: new Date(),
        end: add(new Date(), { months: 4 }),
      }).map((day) => ({
        formDate: day,
        day: formatWithOptions({ locale: es }, "d")(day),
        dayName: formatWithOptions({ locale: es }, "EE")(day),
        date: formatWithOptions({ locale: es }, "yyyy/MM/dd")(day),
        month: formatWithOptions({ locale: es }, "MMMM")(day),
      })),
      counter: 0,

      intervalMonth: eachDayOfInterval({
        start: new Date(),
        end: add(new Date(), { months: 4 }),
      }).map((day) => ({
        firstDay: isFirstDayOfMonth(day) ? day : null,
        lastDay: isLastDayOfMonth(day) ? day : null,
        monthDate: day,
        month: formatWithOptions({ locale: es }, "MMMM")(day),
        year: formatWithOptions({ locale: es }, "d")(day),
        monthDay: formatWithOptions({ locale: es }, "d")(day),
        date: day,
        width: getDaysInMonth(day),
      })),
    },
  }
);
