import React, { useState, useContext, useEffect } from "react";
import { Context as DatesContext } from "../../context/DatesContext";
import TextField from "@mui/material/TextField";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import Button from "@mui/material/Button";

export const SearchPax = ({ onSelectPax }) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const { state, searchNameMsg, selectedRentedMsg } = useContext(DatesContext);
  const { intervalRentedArray } = state.calendar;

  useEffect(() => {
    handleSearch();
  }, [searchTerm]);

  const handleSearch = () => {
    searchNameMsg(searchTerm);

    if (searchTerm.trim() === "") {
      setSearchResults([]);
      return;
    }

    const results = intervalRentedArray.filter((rented) => {
      const searchTermLower = searchTerm.toLowerCase().trim();
      const nameLower = (rented.name || "").toLowerCase();
      const surnameLower = (rented.surname || "").toLowerCase();
      const fullNameLower = `${nameLower} ${surnameLower}`.trim();

      // Check if search term matches name, surname, or full name
      const matchesName = nameLower.includes(searchTermLower);
      const matchesSurname = surnameLower.includes(searchTermLower);
      const matchesFullName = fullNameLower.includes(searchTermLower);

      // Check guest names if available
      const matchesGuestNames =
        rented.guestNames &&
        rented.guestNames.some((guestName) =>
          guestName.toLowerCase().includes(searchTermLower)
        );

      return (
        matchesName || matchesSurname || matchesFullName || matchesGuestNames
      );
    });

    setSearchResults(results);
  };

  const handleSelectPax = (pax) => {
    onSelectPax(new Date(pax.start), pax.id);
  };

  return (
    <div>
      <TextField
        label="Buscar Huesped"
        variant="outlined"
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
      />
      <List>
        {searchResults.map((pax) => (
          <ListItem key={pax.id} button onClick={() => handleSelectPax(pax)}>
            <ListItemText
              primary={`${pax.name} ${pax.surname}`}
              secondary={`Room: ${pax.room}, Check-in: ${pax.start}, Check-out: ${pax.end}`}
            />
          </ListItem>
        ))}
      </List>
    </div>
  );
};
