import React, { useState, useContext } from "react";
import { Context as FormContext } from "../../context/FormContext";
import { useNavigate } from "react-router-dom";
import RegisterForm from "./RegistrationForm";
import { Context as SocketContext } from "../../context/SocketContext";

function LoginForm({ onLogin }) {
  const navigate = useNavigate();
  const { userIdMsg } = useContext(FormContext);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const { socketMsg } = useContext(SocketContext);

  const handleLogin = async (e) => {
    e.preventDefault();
    socketMsg();
    try {
      // const response = await fetch("http://localhost:8000/auth/login", {
      const response = await fetch("/auth/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username, password }),
      });
      const data = await response.json();
      if (response.ok) {
        userIdMsg(data.userId);
        onLogin(); // Call the onLogin function passed from the App component
        navigate("/app"); // Use navigate instead of Navigate component
      } else {
        setError(data.message);
      }
    } catch (error) {
      setError("Error al iniciar sesin");
    }
  };

  return (
    <div>
      <h2>Iniciar Sesin</h2>
      {error && <p style={{ color: "red" }}>{error}</p>}
      <form onSubmit={handleLogin}>
        <div>
          <label>Usuario:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>
        <div>
          <label>Contrasea:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <button type="submit">Iniciar Sesin</button>
      </form>
      <RegisterForm />
    </div>
  );
}

export default LoginForm;
